.move {
  margin: auto;
  min-width: 250px;
  max-width: 250px;
  text-align: left;
  color: white;
  padding: 2px 6px 4px 6px;
}

.category-img {
  cursor: auto;
  border-radius: 25px;
  max-height: 20px;
  max-width: 20px;
  background-color: white;
  vertical-align: middle;
  float: right;
}

.move-category-img {
  max-width: 20px;
  max-height: 20px;
  cursor: auto;
}
